  * REA Group User Interface Library
  * Utility CSS
  * General helper styles
  *
  ---------------------------------------------------------------------------------------------------------------------------------------------------- */
/* Indenting text (eg: hiding the text in browser) */
.rui-text-indent {
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
  display: block; }

/* For image replacement */
.rui-image-replacement {
  background-color: transparent;
  border: 0;
  overflow: hidden;
  /* IE 6/7 fallback */
  *text-indent: -9999px; }

/* For image replacement
 *
 * TODO: Which one is which clown ????
 *
 */
.rui-image-replacement {
  content: "";
  display: block;
  width: 0;
  height: 100%; }

/* Hide for both screenreaders and browsers:
   css-discuss.incutio.com/wiki/Screenreader_Visibility */
.rui-hidden {
  display: none !important;
  visibility: hidden; }

/* Hide only visually, but have it available for screenreaders: by Jon Neal.
  www.webaim.org/techniques/css/invisiblecontent/  &  j.mp/visuallyhidden */
.rui-visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

/* Extends the .visuallyhidden class to allow the element to be focusable when navigated to via the keyboard: drupal.org/node/897638 */
.rui-visuallyhidden.focusable:active,
.rui-visuallyhidden.focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto; }

/* Hide visually and from screenreaders, but maintain layout */
.rui-invisible {
  visibility: hidden; }

/* The epic clearfix for clearing container of floats - new and improved courtesy of http://nicolasgallagher.com/micro-clearfix-hack/ */
/* For modern browsers */
.rui-clearfix:before,
.rui-clearfix:after {
  content: " ";
  display: table; }

.rui-clearfix:after {
  clear: both; }

/* For IE 6/7 (trigger hasLayout) */
.rui-clearfix {
  *zoom: 1; }

/* For clearing floats with an element so it takes up zero space*/
.rui-clearboth {
  border: none;
  clear: both;
  display: block;
  float: none;
  height: 0;
  overflow: hidden;
  visibility: hidden;
  margin: 0;
  padding: 0; }

/* When you just want to clear stuff */
.rui-clear {
  clear: both; }

/* for when you want to truncate elegantly with .... */
.rui-truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  display: block; }

.rui-loading {
  background: #A4A4A4 url("//s1.rui.au.reastatic.net/rui-static/img/loading_scroll.gif") no-repeat 50% 50%;
  background-size: 30px 30px; }

